.coinRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #26272b;
  box-shadow: 0px 0px 12px #18191b;
  border-radius: 8px;
  margin: 2rem 1rem;
  padding: 0.7rem 1rem;
}
.coinRow:hover {
  transform: scale(1.05);
  transition: 0.3s ease-in-out;
  cursor: pointer;
}
img {
  height: 40px;
  margin-right: 8px;
  width: auto;
}
.imgSymbol {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 720px) {
  .hideMobile {
    display: none;
  }
}
