.coinContainer .content {
  max-width: 740px;
  margin: 1rem auto;
  padding: 0.7rem 1rem;
  display: flex;
  flex-direction: column;
  background-color: #26272b;
  box-shadow: 0px 0px 12px #18191b;
  border-radius: 8px;
}
.rank {
  margin: 0.5 0;
}
.rankButton {
  border: 1px solid #6900ff;
  box-shadow: 0px 0px 8px #6900ff;
  background-color: #6900ff;
  border-radius: 8px;
  padding: 0.2rem;
}
.info {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.info .coinHeading {
  display: flex;
  align-items: center;
  margin: 1rem 0;
}
.info .coinPrice {
  display: flex;
  align-items: center;
  justify-content: center;
}
.info p {
  padding-right: 1rem;
}
table {
  margin: 0.5rem 0;
}
td,
th {
  padding: 8px;
  text-align: center;
}
th {
  background-color: #333;
}
.stats {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  width: 100%;
}
.stats .row {
  display: flex;
  justify-content: space-between;
  border-radius: 1px solid #808080;
  margin: 0.6rem 0;
  padding-bottom: 0.5rem;
}
.stats .row p:first-child {
  color: #d3d3d3;
}
.about h3 {
  margin: 1rem 0;
}
.about p a {
  text-decoration: underline;
}
.chart {
  width: 100%;
  height: 300px;
}

@media screen and (max-width: 700px) {
  .coinContainer .content {
    max-width: 100%;
    margin: 0.5rem;
    padding: 0 0.5rem;
  }
  .coinContainer .content .chart {
    padding: 0.7rem 1rem;
  }
  .stats {
    grid-template-columns: 1fr;
  }
  td,
  th {
    padding: 3px;
  }
}
